<template>
	<div class="notifitication">
		<LoadAnimate v-if="loading" :tip="'加载中...'"></LoadAnimate>
		<div class="ttl">主条款:</div>
		<div class="item" v-for="(item,index) in res.data" @click="handleToPdf(item)">{{item.name}}</div>
		<canvas id="pdf-canvas" v-show="showPdf"></canvas>
	</div>
</template>
<script>
import LoadAnimate from '@/components/base/LoadAnimate.vue';
let Base64 = require('js-base64').Base64;
export default {
	data(){
		return {
			loading: false,
			res: {},
			showPdf: false
		}
	},
	created(){
		this.loading = true;
		this.$api.product.getProtocalList({
			id: Base64.decode(this.$route.params.productId)
		})
		.then(res=>{
			console.log('getProtocalList',res);
			this.res = res
			this.loading = false
		});
	},
	components: {
		LoadAnimate
	},
	methods: {
		handleToPdf(item){
			this.$router.push({name: 'claimNotifcationPDF', query: {url: Base64.encode('/pdf'+item.url)}})
		}
	}
}
</script>

<style lang="scss" scoped="scoped">
	@import '@/assets/styles/mixin.scss';
	.notifitication{
		padding: pxToRem(30px);
		font-size: pxToRem(28px);
		color: #1C1D1D;
		line-height: pxToRem(44px);
		position: fixed;
		top: pxToRem(84px);
		bottom: 0;
		left: 0;
		right: 0;
		background: #fff;
		#pdf-canvas{
			position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 10086;
		}
		.ttl{
			margin: pxToRem(30px) 0;
			font-size: pxToRem(32px);
			color:  #1C1D1D;
			line-height: 1.1;
			font-weight: 600;
		}
		.item{
			font-size: pxToRem(28px);
			color: #1C1D1D;
			padding: pxToRem(30px) 0;
			line-height: 1.1;
			border-bottom: pxToRem(2px) solid #EDEDED;
			cursor: pointer;
		}
	}
</style>
